    import share from "@/common/share.js";
	export default {
		data() {
			return {
                style: {
                	pageHeight: 0,
                	contentViewHeight: 0
                }
			}
		},
        onBackPress() {
			//监听back键，关闭弹出菜单
			if (this.shareObj.shareMenu.isVisible()) {
				this.shareObj.shareMenu.hide();
				this.shareObj.alphaBg.hide();
				return true
			}
		},
        computed:{
            loginStatus: function () {
                return this.$store.state.isLogin;
            },
            codetext: function () {
                if (this.loginStatus) {
                    return this.$store.state.userInfo.userCode;
                } else {
                    return "请登录后查看";
                }
            }
        },
        onLoad: function () {
        	const res = uni.getSystemInfoSync();
        	this.style.pageHeight = res.windowHeight;
        	this.style.contentViewHeight = res.windowHeight; //像素
        },
		methods: {
			savePic(){










































            },
            copy(){
                if (this.loginStatus) {














                } else {
                    uni.showToast({
                        title: '请登录后复制',
                        icon: 'none',
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                }
            },
            share(){
                let shareInfo={
                    href:"https://uniapp.dcloud.io",
                    title:"分享测试",
                    desc:"分享测试",
                    imgUrl:"/static/logo.png"
                };
                this.shareObj=share(shareInfo,function(index){
					console.log("点击按钮的序号: " + index);
					let shareObj={
						href:shareInfo.href||"",
						title:shareInfo.title||"",
						summary:shareInfo.desc||"",
						success:(res)=>{
							console.log("success:" + JSON.stringify(res));
						},
						fail:(err)=>{
							console.log("fail:" + JSON.stringify(err));
						}
					};
					switch (index) {
						case 0:
							shareObj.provider="weixin";
							shareObj.scene="WXSceneSession";
							shareObj.type=0;
							shareObj.imageUrl=shareInfo.imgUrl||"";
							uni.share(shareObj);
							break;
						case 1:
							shareObj.provider="weixin";
							shareObj.scene="WXSenceTimeline";
							shareObj.type=0;
							shareObj.imageUrl=shareInfo.imgUrl||"";
							uni.share(shareObj);
							break;
						case 2:
							shareObj.provider="sinaweibo";
							shareObj.type=0;
							shareObj.imageUrl=shareInfo.imgUrl||"";
							uni.share(shareObj);
							break;
						case 3:
							shareObj.provider="qq";
							shareObj.type=1;
							shareObj.imageUrl=shareInfo.imgUrl||"";
							uni.share(shareObj);
							break;
						case 4:
							uni.setClipboardData({
								data:shareInfo.href,
								complete() {
									uni.showToast({
										title: "已复制到剪贴板"
									})
								}
							})
							break;
						case 5:
							plus.share.sendWithSystem({
								type:"web",
								title:shareInfo.title||"",
								thumbs:[shareInfo.imgUrl||""],
								href:shareInfo.href||"",
								content: shareInfo.desc||"",
							})
							break;
					};
				});
				this.$nextTick(()=>{
					this.shareObj.alphaBg.show();
					this.shareObj.shareMenu.show();
				})
           }
		}
	}
